<template>
  <div class="mytodolist">
    <h1>To-do List</h1>
    <form @submit.prevent="addTodo">
      <input v-model="newTodo" placeholder="Add a new Task to-do"> <br>
      <br>
      <button type="submit">Add</button>
      <br> <br>
    </form >
    <div class="my_task">
    <ul>
      <br>
      <li v-for="(todo, index) in todos" :key="index">
        <input type="checkbox" v-model="todo.completed" style="width: 16px; height:16px;">
        <span :class="{ completed: todo.completed }"> <strong>{{ todo.text }}</strong></span> &nbsp;
        <span  v-show="date"> created at: {{ date }} </span> &nbsp;
        <button @click="removeTodo(index)">Remove</button>&nbsp;&nbsp;&nbsp;
      </li>
      <br>
    </ul>
  </div>
 
    <ul>
       <h2> <u>Completed task</u> &nbsp;</h2>
      <h3 v-for="(todo, index) in todos" :key="index">
        <span v-if="todo.completed == true"> {{ todo.text }}</span>
      </h3>
    </ul>
  </div>
</template>
<script>
export default {
    data() {
    return {
            newTodo: '',
            todos: JSON.parse(localStorage.getItem('todos')) || [],
            date: '',
        };
    },
    methods: {
        addTodo() {
            this.todos.push({ text: this.newTodo, complete: false });
            this.newTodo = '';
            this.saveTodos();
      },
        removeTodo(index) {
            this.todos.splice(index, 1);
            this.saveTodos();
        },
        saveTodos() {
            localStorage.setItem('todos', JSON.stringify(this.todos));
        },
        printDate: function () {
            return new Date().toLocaleDateString();
        },
    },
    mounted: function () {
        this.date = this.printDate();
    },
}
</script>

